.iran_sans_font {
	font-family: IRANSans !important;
}

.sama_bg_box,
.sama_bg_box * {
	direction: rtl !important;
	font-family: IRANSans;
}

.sama_bg_box {
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_25___});
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	border: 1px solid rgba(217, 217, 217, 1);
	border-radius: 8px;
}

.sama_main {
	background: linear-gradient(249.92deg, rgba(225, 246, 255, 0.24) 30.88%, rgba(204, 255, 225, 0.24) 103.87%);
	background-size: cover;
}

.sama_main_content {
	padding: 2rem;
	/*width: 90%;
	margin: auto;*/
	display: flex;
}

.sama_header_box {
	/*
	display: flex;
	align-items: stretch;
	*/
	width: 17%;
}

.logo_sama img {
	width: 100%;
}

.logo_sama {
	margin-bottom: 1.3rem;
	width: 35%;
	margin-left: auto;
	display: flex;
	align-items: center;
	justify-content: center;
}

.sama_link_read_more {
	margin-right: auto;
	display: flex;
	align-items: end;
	font-size: 16px;
	font-weight: 500;
	line-height: 16px;
	text-align: left;
	color: rgba(0, 168, 232, 1);
	cursor: pointer;
}

.sama_link_read_more i {
	margin-right: 1rem;
	line-height: 16px;
	transform: scaleX(-1);
	color: rgba(0, 168, 232, 1);
}

.sama_head_title {
	display: flex;
	align-items: end;
	font-size: 16px;
	font-weight: 800;
	line-height: 24px;
	text-align: right;
}

.sama_head_title {
	color: rgba(25, 70, 137, 1);
}

.sama_boxs_holder {
	display: flex;
	width: 90%;
	align-items: stretch;
	justify-content: space-between;
}

.sama_option {
	backdrop-filter: blur(3px);
	width: 49%;
	border: 1px solid rgba(25, 70, 137, 1);
	border-radius: 6px;
	padding: 16px;
	display: flex;
	align-items: stretch;
}

.payment_icon {
	width: 8%;
}

.payment_descriptions {
	width: 100%;
}

.payment_select_btn_side {
	width: 50%;
	display: flex;
	margin-right: auto;
	justify-content: end;
	align-items: end;
}

.payment_icon img {
	width: 70%;
	margin-left: auto;
}

.payment_title {
	font-size: 20px;
	font-weight: 600;
	line-height: 30px;
	text-align: right;
	color: rgba(25, 70, 137, 1);
	margin-bottom: 10px;
	display: flex;
	justify-content: space-between;
}

.info_item_sama {
	display: flex;
	width: 100%;
	align-items: center;
	margin-bottom: 1px;
	color: rgba(25, 70, 137, 1);
	font-size: 16px;
	font-weight: 500;
	line-height: 24px;
	text-align: right;
}

.info_item_sama:last-child {
	margin-bottom: 0px;
}

.payment_select_button {
	background: rgba(0, 150, 89, 1);
	padding: 7px 13px;
	font-size: 14px;
	font-weight: 500;
	line-height: 24px;
	text-align: right;
	color: #fff;
	border-radius: 5px;
	cursor: pointer;
}

.sama_icon_item img {
	width: 80%;
}

.sama_icon_item {
	display: flex;
	width: 5%;
	align-items: center;
	justify-content: right;
	margin-left: 10px;
}

.sama_option.selected_option_sama {
	border: 1px solid rgba(0, 150, 89, 1);
	background: rgba(0, 150, 89, 0.08);
}

.info_item_sama_text {
	opacity: 80%;
	width: calc(95% - 10px);
}

.payment_select_button_remove img {
	width: 90%;
	margin: auto;
}

.payment_select_button_remove {
	width: 28%;
	background: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 5px;
	border-radius: 8px;
	border: 1px solid rgba(217, 217, 217, 1);
	cursor: pointer;
}

/* this is for themes that use --wp--style--global--content-size for checkout page */
.woocommerce-checkout .woocommerce {
	max-width: 1200px;
}

@media only screen and (max-width: 1199px) {
	.woocommerce-checkout .woocommerce {
		max-width: 720px;
	}
}

@media only screen and (max-width: 700px) {
	.woocommerce-checkout .woocommerce {
		max-width: 380px;
	}
}



/* responsive part (phone responsive 700 px) */
@media only screen and (max-width: 700px) {
	.woocommerce-checkout .woocommerce {
		max-width: 380px;
	}

	.sama_main_content {
		display: block;
		padding: 1rem;
	}

	.sama_header_box {
		width: 100%;
		display: flex;
		align-items: center;
	}

	.sama_boxs_holder {
		display: block;
		width: 100%;
	}

	.sama_option {
		width: 100%;
		padding: 0;
		margin-bottom: 1rem;
	}

	.logo_sama {
		margin-bottom: 0rem;
		width: 10%;
		margin-left: 0.5rem;
	}

	.sama_header_box {
		margin-bottom: 1rem;
	}

	.payment_title {
		width: 100%;
		margin: auto;
		margin-bottom: 1rem;
		font-size: 12px;
	}

	.payment_select_button {
		font-size: 10px;
	}

	.payment_descriptions {
		padding: 1rem;
	}

	.payment_title>div {
		display: flex;
		align-items: center;
	}

	.info_item_sama_text {
		font-size: 12px;
	}

	.payment_select_button_remove {
		width: 25%;
		padding: 4px;
	}

	.payment_select_button_remove img {
		width: 100%;
		margin: auto;
		padding: 0px 0px;
	}

	.sama_head_title {
		width: 53%;
	}
}



.disabled_by_sama_plugin * {
	cursor: no-drop;
}

#sama_payment_type_f_field {
	display: none !important;
}

.disabled_by_sama_plugin {
	background: #5552;
}

#sama_payment_type_f {
	display: none !important;
}


.disabled_payments {
	filter: brightness(0.8);
	background: #fff;
	cursor: no-drop;
}

.sama_bg_box {
	width: 1200px;
